/* Greeting Font */
/* 'M PLUS Rounded 1c', sans-serif */

* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

#root {
   --main-color: #1e90ff;
   --greeting-font: "M PLUS Rounded 1c", sans-serif;
   font-family: "Rubik", sans-serif;
   background-color: #eff7ff;
}

@media (min-width: 1024px) {
   html {
      font-size: 17px;
   }
}

.home {
   width: 100%;
   min-height: 100vh;
   display: grid;
   grid-template-columns: 1fr 1fr;
   grid-template-rows: 1fr 1fr;
   overflow: hidden;
}

.home .wave-container {
   grid-column: 1 / 3;
   grid-row: 2 / 3;
   align-self: end;
}

.home .float-items {
   grid-column: 1 / 3;
   grid-row: 1 / 3;
}

.home .navbar {
   grid-column: 1 / 3;
   grid-row: 1 / 2;
   z-index: 2;
}

.home .greeting {
   grid-column: 1 / 3;
   grid-row: 1 / 3;
   z-index: 1;
   margin-bottom: 10vh;
}

.home .avatar {
   grid-column: 2 / 3;
   grid-row: 1 / 3;
   opacity: 0;
   margin-bottom: 10vh;
   transition: opacity 0.5s;
}

@media (min-width: 1024px) {
   .home .greeting {
      grid-column: 1 / 2;
   }
   .home .avatar {
      opacity: 1;
   }
}

.wave-container {
   position: relative;
   width: 100%;
   height: 180px;
   overflow: hidden;
}

.wave-container .wave-boat {
   position: absolute;
   bottom: 75px;
   left: 15%;
   width: 5.5rem;
   height: 5.5rem;
   /* background: url("../../../media/image/boat.png"); */
   /* background-size: 125px 125px; */
   z-index: 1001;
   animation: wave-boatMove 5s linear infinite;
}
@keyframes wave-boatMove {
   0% {
      transform: translateY(0);
   }
   30% {
      transform: translateY(40px) translateX(20px) rotate(10deg);
   }
   55% {
      transform: translateY(60px) translateX(40px) rotate(-8deg);
      animation-timing-function: ease-in-out;
   }
   100% {
      transform: translateY(0);
   }
}

.wave-container .wave {
   position: absolute;
   bottom: 0;
   left: 0;
   width: 100%;
   height: 100px;
   background: url(data:image/png;base64,);
   background-size: 100vw 100px;
}

.wave-container .wave1 {
   animation: wave-waveMove 5s linear infinite;
   z-index: 1000;
   opacity: 0.5;
}

.wave-container .wave2 {
   animation: wave-waveMove 8s linear infinite;
   animation-direction: reverse;
   z-index: 999;
   opacity: 0.4;
   animation-delay: -5s;
   background-size: 100vw 110px;
}

.wave-container .wave3 {
   animation: wave-waveMove 12s linear infinite;
   z-index: 998;
   opacity: 0.35;
   animation-delay: -10s;
   background-size: 100vw 110px;
}

.wave-container .wave4 {
   animation: wave-waveMove 15s linear infinite;
   animation-direction: reverse;
   z-index: 997;
   opacity: 0.3;
   animation-delay: -15s;
   background-size: 100vw 110px;
}

@keyframes wave-waveMove {
   0% {
      background-position-x: 100vw;
   }
   100% {
      background-position-x: 0;
   }
}

.float-items {
   position: relative;
}

.float-items .float-item {
   position: absolute;
   max-width: 7.5rem;
   max-height: 7.5rem;
}

.float-items .js-ballon {
   top: 65%;
   left: 66%;
   animation: move-balloon 6s ease-in-out infinite;
   transform-origin: bottom center;
}

@keyframes move-balloon {
   0%,
   100% {
      transform: translateY(0) rotate(3deg);
   }
   50% {
      transform: translateY(-20px) rotate(-3deg) scale(1.1);
   }
}

.float-items .js-astronaut {
   top: 15%;
   left: 45%;
   animation: rotate-astronaut 5s ease-in-out infinite;
}

@keyframes rotate-astronaut {
   0%,
   100% {
      transform: rotate(10deg) translateX(-20px);
   }

   50% {
      transform: rotate(-10deg) translateX(20px);
   }
}

@media (min-width: 1024px) {
   .float-items .js-ballon {
      left: 86%;
   }
   /* .float-items .float-item {
      max-width: 135px;
      max-height: 135px;
   } */
}

.navbar {
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 0 10vw;
   padding-top: 3vh;
   height: 100px;
}

/** Logo */
.navbar .nav-logo img {
   width: 3.2rem;
   height: 3.2rem;
   transition: transform 0.3s;
}
.navbar .nav-logo img:hover {
   transform: scale(1.2);
}

/** Right */
.navbar .nav-right {
   display: flex;
   list-style: none;
   /* Hover Animation */
   /* margin-bottom: -58px; */
   margin-top: 39px;
}

.navbar a {
   text-decoration: none;
   color: var(--main-color);
   font-weight: 500;
}

.navbar .js-navItem {
   /* Hover Animation */
   display: flex;
   flex-direction: column;
   align-items: center;
}

.navbar .nav-right svg {
   width: 2rem;
   height: 2rem;
   transition: opacity 0.3s, transform 0.3s;
}

.navbar .nav-project svg {
   width: 2.1rem;
   height: 2.1rem;
}

.navbar a p {
   opacity: 0;
   transition: opacity 0.6s, transform 0.3s;
}

/** Hover Animation */
.navbar a:hover svg {
   transform: translateY(-30px);
   opacity: 0;
}

.navbar a:hover p {
   opacity: 1;
   transform: translateY(-26px);
}

/* Skills and About complete */
/* .navbar a:hover .nav-skills p {
   opacity: 1;
   transform: translateY(-46px);
} */

.navbar a:hover .nav-skills p,
.navbar a:hover .nav-contact p {
   opacity: 1;
   transform: translateY(-35px);
}

@media (min-width: 768px) {
   .navbar a {
      margin: 0 10px;
   }
}

/* Bitter Greeting Font */

.greeting {
   display: flex;
   justify-content: center;
   align-items: center;
   flex-wrap: wrap;
}

.greeting .greeting-animatedLetters {
   min-width: 320px;
   height: 210px;
   margin: 0 5vw;
   display: inline-block;
}

.greeting .js-greeting-letter {
   font-size: 2.3rem;
   font-family: var(--greeting-font);
   font-weight: 700;
   display: inline-block;
   transition: color 0.3s ease-out;
   color: var(--main-color);
}

.greeting .space {
   margin-right: 12px;
}

.greeting .js-greeting-slogan {
   margin-top: 5px;
   color: var(--main-color);
   /* emoji */
   display: flex;
}
/* max-width: 399px;
} */

.greeting .js-greeting-slogan p {
   line-height: 30px;
}
