/* emoji */
.greeting .greeting-emoji {
   display: inline-block;
   margin-left: 3px;
   font-size: 1.5em;
   transition: all 0.3s;
}

.greeting .greeting-emoji:hover {
   transform: scale(1.2);
}

@media (min-width: 1024px) {
   .greeting .greeting-animatedLetters {
      min-width: 390px;
   }

   .greeting .js-greeting-letter {
      font-size: 2.8rem;
   }
   .greeting .js-greeting-slogan p {
      max-width: none;
   }
}

.greeting .js-hover-rubberBand {
   animation: greeting-rubberBand 1.5s;
   color: #00d4fa;
}

.greeting .js-bounceIn {
   animation-name: greeting-bounceIn;
   animation-duration: 0.75s;
   animation-duration: 1s;
   animation-fill-mode: both;
}

@keyframes greeting-rubberBand {
   from {
      transform: scale3d(1, 1, 1);
   }

   30% {
      transform: scale3d(1.25, 0.75, 1);
   }

   40% {
      transform: scale3d(0.75, 1.25, 1);
   }

   50% {
      transform: scale3d(1.15, 0.85, 1);
   }

   65% {
      transform: scale3d(0.95, 1.05, 1);
   }

   75% {
      transform: scale3d(1.05, 0.95, 1);
   }

   to {
      transform: scale3d(1, 1, 1);
   }
}
@keyframes greeting-bounceIn {
   0%,
   20%,
   40%,
   60%,
   80%,
   100% {
      transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
   }
   0% {
      opacity: 0;
      transform: scale3d(0.3, 0.3, 0.3);
   }
   20% {
      transform: scale3d(1.1, 1.1, 1.1);
   }
   40% {
      transform: scale3d(0.9, 0.9, 0.9);
   }
   60% {
      opacity: 1;
      transform: scale3d(1.03, 1.03, 1.03);
   }
   80% {
      transform: scale3d(0.97, 0.97, 0.97);
   }
   100% {
      opacity: 1;
      transform: scale3d(1, 1, 1);
   }
}

