   display: flex;
   justify-content: center;
   align-items: center;
   padding-right: 12vw;
}

.avatar svg {
   width: 23vw;
   min-width: 300px;
   max-height: 500px;
}

.playGround {
   background-color: white;
   width: 100%;
   min-height: 100vh;
   display: grid;
   grid-template-columns: 1fr 1fr;
   grid-template-rows: 1fr 1fr;
}

.playGround .waveContaienr {
   grid-column: 1 / 3;
   grid-row: 2 / 3;
   align-self: end;
}

.playGround .floatItemContainer {
   grid-column: 1 / 3;
   grid-row: 1 / 3;
